<template>
  <div v-resize="calcHeight">
    <div ref="resizableDiv">
      <slot name="table" :table-height="tableHeight" />
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResizableView',
  data() {
    return {
      tableHeight: 0,
    }
  },
  props: {
    footerHeight: {
      default: 59, // 59 is default v-data-table footer height
      type: Number,
    },
    headerHeight: {
      default: 64, // 64 is default v-data-table header height
      type: Number,
    },
  },
  methods: {
    calcHeight() {
      this.tableHeight =
        window.innerHeight -
        this._props.headerHeight -
        this._props.footerHeight -
        this.$refs.resizableDiv.getBoundingClientRect().y -
        this.footerHeight
    },
  },
}
</script>
